.product__image img{
    width: 100%;
    height: 100%;
}

.product__image {
    padding: 10px;
    border: 1px dashed var(--color-red);
    border-radius: 9px;
}

.product__content {
    display: flex;
    align-items: flex-start;
}

.product__head {
    display: flex;
    align-items: stretch;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 9px;
    width: 100%;
    background-color: #fff;
}
.product__count input{
    margin: 0 5px;
    width: 100%;
}
.product__count button {
    background-color: var(--color-gray);
    padding: 7px 20px;
    border: none;
    font-size: 16;
    font-weight: 600;
    border-radius: 2px;
}

.product__info input {
    background-color: var(--color-gray);
    padding: 7px 5px;
    border: none;
    border-radius: 2px;
    text-align: center;
}

.product__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px dashed #000;
}

.product__info {
    background-color: #fff;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    position: relative;
    border-radius: 9px;
}

.product__count {
    margin: 20px 0;
    text-align: center;
    display: flex;
    justify-content: center;
}
.product__cart i{
padding-left: 5px;
}
.product__cart {
    border: 1px solid var(--color-red);
    background-color: var(--color-red);
    color: white;
    width: 100%;
    font-size: 16;
    font-weight: 600;
    border-radius: 2px;
    padding: 10px;
}

.product__cart:active{
    background-color: var(--color-red) !important;
    color: white;
}
.product__cart--active{
    color: var(--color-red);
    background-color: white !important;
}

.product__desc {
    padding: 20px;
    width: 100%;
    position: relative;
}

.product__desc h3 {
    margin-top: 20px;
    margin-bottom: 10px;
}

.product__desc p {
    text-align: justify;
}

.product__time{
    padding-top: 10px;
    color: var(--color-red);
}