.products__content {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.product__item-price span:first-child {
    text-decoration: line-through;
    color: rgb(169, 169, 169);
    font-weight: normal;
}

.product__item-price span {
    display: block;
}

.product__item-price {
    color: var(--color-red);
    font-size: 20px;
    font-weight: 600;
    text-align: right;
    position: absolute;
    right: 10px;
    bottom: 10px;
}

.products__list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, auto);
    gap: 15px;
    justify-items: center;
}

.product__item-content h3 {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 15%;
    text-align: center;
}

.product__item-count {
    letter-spacing: 1.7px;
    color: white;
    border-radius: 9px;
    padding: 5px;
    position: absolute;
    top: 15px;
    left: 15px;
    font-size: 19px;
    font-weight: 600;
    background-color: var(--color-red);
}

.product__item {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    width: 100%;
    padding: 10px;
    transition: .1s;
    border-radius: 9px;
    position: relative;
    aspect-ratio: 1;
    background-color: #fff;
}

.product__item a {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.product__item:hover {
    box-shadow: 0px 0px 15px rgba(196, 61, 61, 0.15);
}

.product__item-content {
    margin-top: 10px;
    color: black;
}

.product__item-image {
    text-align: center;
    width: 100%;
    margin-top: 50px;
}

.product__item-image img {
    width: 40%;
    height: 100%;
}

.products__aside {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 9px;
    background-color: #fff;
}

.products__aside input {
    display: block;
    margin: 0 auto 10px;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 9px 9px 0 0;
    border-bottom: 1px dashed #000;
}

.products__aside-category {
    margin-top: 20px;
}

.products__aside-category h3 {
    padding: 5px 10px;
}

.products__aside-category ul button {
    width: 100%;
    border: none;
    background-color: transparent;
    text-align: left;
    padding: 10px;
    transition: .1s;
    font-weight: 500;
    text-transform: capitalize;
}

.products__aside-category ul button:hover {
    background-color: var(--color-gray);
}

.products__aside--active {
    background-color: var(--color-red) !important;
    color: white;
}

.products__list-message {
    width: 100%;
    text-align: center;
}