:root {
    --font-title: 'Staatliches', cursive;
    --font-p: 'Hind Madurai', sans-serif;
    --color-red: rgb(196, 61, 61);
    --color-gray: rgb(236, 236, 236);
}

body {
    font-family: var(--font-p);
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

button {
    cursor: pointer;
}

section {
    margin: 60px 0 20px;
}

aside {
    width: 350px;
}

button {
    background-color: transparent;
    border: none;
}


.slide-in-enter-active {
    animation: modalOpen .2s ease-in forwards;
}

.slide-in-exit-active {
    animation: modalClose .2s ease-out forwards;
}

@keyframes modalOpen {
    0% {
        transform: translate(50%, -50px);
        opacity: 0;
    }
    100% {
        transform: translate(50%, 0);
        opacity: 1;
    }
}

@keyframes modalClose {
    0% {
        opacity: 1;
        transform: translate(50%, 0);
    }
    100% {
        transform: translate(50%, -50px);
        opacity: 0;
    }
}

body{
    position: relative;
}

.background {
    z-index: -1;
    position: fixed;
    width: 100%;
    top: 0;
    bottom: 0;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    opacity: .7;
    height: 100%;
    filter: blur(1px);
}

.message {
    color: white;
    position: absolute;
    background-color: rgba(196, 61, 61, .9);
    padding: 5px 30px;
    border-radius: 5px;
    top: 10px;
    right: 50%;
    transform: translateX(50%);
    text-align: center;
    width: 50%;
    transition: all .2s ease-out;
}

.message button {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    background: transparent;
    border: none;
    color: white;
}