
@media screen and (max-width: 768px) {
    .wrapper {
        width: 100%;
    }
    .header {
        justify-content: center;
        height: 60px;
    }
    .header__content:nth-child(2){
        display: none;
    }
    .products__content {
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        padding: 10px;
    }
    .products__aside {
        width: 100%;
        margin-bottom: 20px;
    }
    .products__list {
        grid-template-columns: repeat(2, auto);
        gap: 10px;
    }
    .product__item-count {
        left: 10px;
        top: 10px;
        font-size: 10px;
        border-radius: 5px;
    }
    .product__item-content h3 {
        font-size: 10px;
    }
    .product__item-price span:first-child{
        font-size: 10px;
    }
    .product__item-price span:last-child {
        font-size: 13px;
    }
    .product__content{
        flex-direction: column-reverse;
        padding: 10px;
    }
    .product__info{
        width: 100%;
        margin: 0;
        padding: 10px;
        margin: 20px 0;
    }
    .product__head{
        flex-direction: column;
    }
    .cart__content{
        flex-direction: column;
        padding: 10px;
    }
    .cart__item-buttons span{
        margin-right: 30px;
        font-size: 16px;
        border-radius: 5px;
    }
    .cart__aside{
        width: 100%;
    }
}

@media screen and (min-width:400px) and (max-width: 500px){
    .product__item-content h3 { 
        font-size: 15px;
    }
}

@media screen and (min-width: 500px){
    .product__item-content h3 { 
        font-size: 18px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .wrapper {
        width: 100%;
    }
    .header {
        justify-content: center;
        height: 60px;
    }
    .header__content:nth-child(2){
        display: none;
    }
    .products__content {
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        padding: 10px;
    }
    .products__aside {
        width: 100%;
        margin-bottom: 20px;
    }
    .cart__content{
        padding: 10px;
    }
    .product__item-image img {
        width: 50%;
        height: 100%;
    }
    .products__list {
        padding: 0;
        grid-template-columns: repeat(3, auto);
        gap: 10px;
    }
    .product__item-count {
        left: 10px;
        top: 10px;
        font-size: 16px;
    }
    .product__info{
        padding: 20px;
    }
    .product__item {
        padding: 0px;
    }
    .product__content{
        padding: 10px;
    }
    .product__head{
        margin-right: 20px;
    }
    .product__item-price span:first-child {
        font-size: 14px;
    }
    .product__item-price span:last-child {
        font-size: 18px;
    }
}

